<template>
  <div>
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs3 v-for="movie in orders" :key="movie.Id">
          {{movie}}
        </v-flex>
      </v-layout>
    </v-container>

    <v-dialog width="300" v-model="loading">
      <v-card color="red darken-3" dark>
        <v-card-text>
          Loading Orders...
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

  import MS from '../firebaseCRUD'

  export default {
    beforeMount(){
      this.loading = true;
      setTimeout(() => {this.getOrders()}, 2000)
    },
    components: {
    },
    props: {
      ActiveUser: Object
    },
    data() {
      return {
        subColor: "#ab302f",
        orders: [],
        loading: false
      };
    },
    methods: {
      getOrders(){
        MS.getOrders(this.ActiveUser.key).then(orders => {
           this.orders = orders;
        });
        console.log(this.orders);
        this.loading = false;
      }
    }
  };
</script>

<style scoped>
  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;

  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



